const $ = function (selector) {
	return new $.prototype.init(selector);
};

$.prototype.init = function (selector) {
	if (!selector) {
		return this; // {}
	}

	if (selector.tagName) { // если в качестве селектора передается html элемент то
		this[0] = selector;
		this.length = 1;
		return this;
	}

	Object.assign(this, document.querySelectorAll(selector));
	this.length = document.querySelectorAll(selector).length;
	return this;
};

$.prototype.init.prototype = $.prototype; // Важно! Позволяет нашему полученному элементу($) исп. методы

window.$ = $;

export default $;