import $ from '../core';

$.prototype.animateOverTime = function(dur, cb, fin) {
	let timeStart;
	
	function _animateOverTime(time) {	// тех. ф-ия
		if (!timeStart) {	// если не уст. timeStart 
			timeStart = time;	// по сути начало нашей ф-ии, time будет очень маленький почти 0
		}

		let timeElapsed = time - timeStart;	// отслеживает выполнение анимации, время анимации - время начала анимации
		let complection = Math.min(timeElapsed / dur, 1);	// изменение параметров на странице, от 0.01 до 1

		cb(complection);	// callback, будет меняться opacity взависимости от фор-лы

		if (timeElapsed < dur) {	// длительность анимации < времени анимации
			requestAnimationFrame(_animateOverTime);	// запускаем ф-ию
		} else {
			if (typeof fin === 'function') {	// если аргумент fin будет ф-ей, то запустим ее
				fin();
			}
		}
	}

	return _animateOverTime;
};

$.prototype.fadeIn = function(dur, display, fin) {	// обязательный только dur - длительность анимации, dispaly автоматически подставляется, fin не обязат.
	for (let i = 0; i < this.length; i++) {
		this[i].style.display = display || 'block';	// способ установки знач. по умолч., false || true, оператор || выбирает true, то есть block, также  можно написать в аргумент display = 'block
	
		const _fadeIn = (complection) => {	// важно! должна быть стрелочная ф-ия, т.к необходим контекст вызова this
			this[i].style.opacity = complection;	// будет менять opacity от 0 до 1 взависимости от complection(время)
		};

		const ani = this.animateOverTime(dur, _fadeIn, fin);	// запускаем ф-ию с 3 арг.
		requestAnimationFrame(ani);	// запускаем анимацию	
	}

	return this;
};

$.prototype.fadeOut = function(dur, fin) {	
	for (let i = 0; i < this.length; i++) {

		const _fadeOut = (complection) => {
			this[i].style.opacity = 1 - complection;	// элемент будет становится прозрачнее и до 0

			if (complection === 1) {	// если элемент стал полностью прозрачным то
				this[i].style.display = 'none'; 	// мы его скрываем со странице
			}

		};

		const ani = this.animateOverTime(dur, _fadeOut, fin);	
		requestAnimationFrame(ani);
	}

	return this;
};

$.prototype.fadeToggle = function(dur, display, fin) {
	for (let i = 0; i < this.length; i++) {
		if (window.getComputedStyle(this[i]).display === 'none') {
			$(this[i]).fadeIn(dur, display, fin);
		} else {
			$(this[i]).fadeOut(dur, fin);
		}
	}

	return this;

};