import $ from '../core';

$.prototype.tab = function () {
	for( let i = 0; i < this.length; i++) {	// актив класс показывает контент
		$(this[i]).on('click', () => {	// по клику на таб добавим ему класс активности и удалим кл.актив у других табов
			$(this[i]) 
				.addClass('tab-item--active')	// добавляем кл.актив табу
				.siblings().removeClass('tab-item--active')	// удаляем кл.актив у соседних табов
				.closest('.tab').find('.tab-content').removeClass('tab-content--active')	// ищем таб контенты и удаляем кл. актив
				.eq($(this[i]).index()).addClass('tab-content--active');	// ищем нужный номер контента и добавляем ему кл.актив
		});
	}
};

$('[data-tabpanel] .tab-item').tab();