import $ from '../core';

$.prototype.modal = function(created) {	// created- аргумент для метода createModal который отслеживает было ли создано модальное окно програмно не через верстку(через createModal)
	for (let i = 0; i < this.length; i++) {
		const target = this[i].getAttribute('data-target');
		$(this[i]).click((e) => {
			e.preventDefault();
			$(target).fadeIn(500);
			document.body.style.overflow = 'hidden';
		});

		const closeElements = document.querySelectorAll(`${target} [data-close]`);	// Закрытие модального через data-close( крестик, кнопка)
		closeElements.forEach((item) => {		// Ищет data-close у target(модального окна), специфичного окна,а не всех
			$(item).click(()=> {
				$(target).fadeOut(500);
				document.body.style.overflow = '';
				if (created) { // если модального окно создано программно( с помощью createModal)
					document.querySelector(target).remove(); // то не только закрываем а еще удаляем а то будет наложение окон после куча кликов
				}
			});
		});
		
		$(target).click((e) => {		// Закрытие кликом на подложку модального окна
			if (e.target.classList.contains('modal')) {
				$(target).fadeOut(500);
				document.body.style.overflow = '';
				if (created) {	
					document.querySelector(target).remove();
				}
			}
		});
	}
};

$('[data-toggle="modal"]').modal();


$.prototype.createModal = function({text, btns} = {}) {	// динамическое создание modal-окна
	for (let i = 0; i < this.length; i++) {
		let modal = document.createElement('div');		// создадим блок modal
		modal.classList.add('modal');					// добавим ему класс
		modal.setAttribute('id', this[i].getAttribute('data-target').slice(1)); // атрибут, значение( получаем значение из нашего тригера), slice(1) удаляет # из data-target='#exampleModal'
		
		// btns = {count:num, settings: [[text, classNames=[], close, cb]]}	// это наша кнопка в модальном окне
		const buttons = [];
		for (let j = 0; j < btns.count; j++) {
			let btn = document.createElement('button');
			btn.classList.add('btn', ...btns.settings[j][1]); // добавим класс btn и классы по типу btn-primary или activ, взависимости от btns.settings.classNames =
			btn.textContent = btns.settings[j][0];	// [j] - номер перебираемого элемента, [0] это 1-ая настройка в settings то есть text
			if (btns.settings[j][2]) {				// если передаем аргумент close то добавим атрибут
				btn.setAttribute('data-close', 'true');
			}
			if (btns.settings[j][3] && typeof(btns.settings[j][3]) === 'function') {	// если передаем call back ф-ии(проверим является ли передаваемый аргумент ф-ией)
				btn.addEventListener('click', btns.settings[j][3]); 
			}

			buttons.push(btn); 
		}

		modal.innerHTML = `
			<div class="modal-dialog">
				<div class="modal-content">
					<button class="close" data-close>
						<span>&times;</span>
					</button>
					<div class="modal-header">
						<div class="modal-title">
							${text.title}
						</div>
					</div>
					<div class="modal-body">
						${text.body}
					</div>
					<div class="modal-footer">
						
					</div>
				</div>
			</div>
		`;

		modal.querySelector('.modal-footer').append(...buttons);	// добавим наши кнопки в блок modal-footer
		document.body.appendChild(modal); // поместим наше модальное окно на страницу
		$(this[i]).modal(true);  // после createmodal подвяжет modal
		$(this[i].getAttribute('data-target')).fadeIn(500);	// вызываем 
	}
};