import $ from '../core';

$.prototype.show = function () {
	for (let i = 0; i < this.length; i++) {
		if (!this[i].style) { // есть ли такой параметр как style у перебираемого элемента
			continue;
		}
		this[i].style.display = '';
	}

	return this; // возвращаем чтобы дальше можно было с ним работать(исп.методы и тд)
};

$.prototype.hide = function () {
	for (let i = 0; i < this.length; i++) {
		if (!this[i].style) {
			continue;
		}
		this[i].style.display = 'none';
	}

	return this;
};

$.prototype.toggle = function () {
	for (let i = 0; i < this.length; i++) {
		if (!this[i].style) {
			continue;
		}

		if (this[i].style.display === 'none') { // Тоглим, если нет то будет, если есть то исчезнет
			this[i].style.display = '';
		} else {
			this[i].style.display = 'none';
		}
	}

	return this;
};