import $ from '../core';

$.prototype.accordion = function(headActive = 'accordion-head--active', contentActive = 'accordion-content--active',
paddings = 40) {
	for (let i = 0; i < this.length; i++) {
		$(this[i]).click( () => {
			$(this[i]).toggleClass(headActive);	// Тоглим класс актив по нажатию
			$(this[i].nextElementSibling).toggleClass(contentActive);	// След элемент по верстке, тоглим класс

			if ( this[i].classList.contains(headActive)) {
				this[i].nextElementSibling.style.maxHeight = this[i].nextElementSibling.scrollHeight + paddings + 'px';
			} else {
				this[i].nextElementSibling.style.maxHeight = '0px';
			}
		});
	}
};

$('.accordion-head').accordion();