import $ from '../core';

$.prototype.get = async function (url, dataTypeAnswer = 'json') { // dataTypeAnswer - формат данных
	let res = await fetch(url); // отправляем фетч запрос на юрл(сервер), await ожидает завершение запроса

	if (!res.ok) {
		throw new Error(`Could not fetch ${url}, status${res.status}`);
	}

	switch (dataTypeAnswer) {
		case 'json':
			return await res.json();
		case 'text':
			return await res.text();
		case 'blob':
			return await res.blob();
	}
};

$.prototype.post = async function (url, data, dataTypeAnswer = 'text') { // POST запрос
	let res = await fetch(url, { // также делаем фетч запрос на сервер(постим данные)
		method: "POST",
		body: data,
	});

	switch (dataTypeAnswer) { // обрабатываем данные
		case 'json':
			return await res.json();
		case 'text':
			return await res.text();
		case 'blob':
			return await res.blob();
	}
};