import $ from '../core';

$.prototype.carousel = function () {
	for (let i = 0; i < this.length; i++) {
		const width = window.getComputedStyle(this[i].querySelector('.carousel-inner')).width;
		const slides = this[i].querySelectorAll('.carousel-item');
		const slidesField = this[i].querySelector('.carousel-slides');
		const dots = this[i].querySelectorAll('.carousel-indicators li');

		slidesField.style.width = 100 * slides.length + '%'; // устанавливаем ширину блока слайдов в 300% в нашем случае(% зависит от кол-во слайдов)
		slides.forEach(slide => {
			slide.style.width = width;
		});

		let offset = 0;	// отвечает за смещение наших слайдов
		let slideIndex = 0;	// отвечает за активный слайд (для точек)

		$(this[i].querySelector('[data-slide="next"]')).click((e) => {	// нажатие на стрелку
			e.preventDefault();
			if (offset == (+width.replace(/\D/g, '') * (slides.length - 1))) {	// убираем все НЕчисла с ширины и сравниваем
				offset = 0;
			} else {
				offset += +width.replace(/\D/g, '');
			}

			slidesField.style.transform = `translateX(-${offset}px)`;	// перемещаем  на какое-то число(ширину слайда) в влево

			if (slideIndex == slides.length - 1) {	// Отслеживает активный слайд для точек
				slideIndex = 0;
			} else {
				slideIndex++;
			}
			dots.forEach(dot => dot.classList.remove('active'));	// удаляем со всех точек активность
			dots[slideIndex].classList.add('active');
		});

		$(this[i].querySelector('[data-slide="prev"]')).click((e) => {
			e.preventDefault();
			if (offset == 0) {
				offset = +width.replace(/\D/g, '') * (slides.length - 1);
			} else {
				offset -= +width.replace(/\D/g, '');
			}

			slidesField.style.transform = `translateX(-${offset}px)`;

			if (slideIndex == 0) {
				slideIndex = slides.length - 1;
			} else {
				slideIndex--;
			}
			dots.forEach(dot => dot.classList.remove('active'));	// удаляем со всех точек активность
			dots[slideIndex].classList.add('active');
		});

		const sliderId = this[i].getAttribute('id');	// Для нажатия по точкам 
		$(`#${sliderId} .carousel-indicators li`).click(e => {
			const slideTo = e.target.getAttribute('data-slide-to');	// получим значение атрибута data-slide-to

			slideIndex = slideTo;	// и номер слайда будет равен значению атрибута
			offset = +width.replace(/\D/g, '') * slideTo;	// отступ будет равен ширине каждого слайда умнож на кол-во слайдов которое необходимо пролистать

			slidesField.style.transform = `translateX(-${offset}px)`;
			dots.forEach(dot => dot.classList.remove('active'));
			dots[slideIndex].classList.add('active');
		});
	}
};

$('.carousel').carousel();