import $ from '../core';

$.prototype.html = function(content) {	// innerHTML
	for (let i = 0; i < this.length; i++) {
		if (content) {
			this[i].innerHTML = content;	// заменяем контент
		} else {
			return this[i].innerHTML;		// получаем контент если не передан аргумент
		}
	}

	return this;
};

$.prototype.eq = function(i) {	// eq - позволяет получить определенный элемент( допустим вторую кнопку) i - номер
	const swap = this[i];		// искомый элемент
	const objLength = Object.keys(this).length;	// позволяет получить кол-во элементов в массиве 

	for (let i = 0; i < objLength; i++) {
		delete this[i];	// очищаем массив 
	}

	this[0] = swap;	// затем помещаем наш искомый элемент
	this.length = 1;
	return this;
};

$.prototype.index = function() {	// индекс элемента среди элементов с общим родителем
	const parent = this[0].parentNode;	// получаем родителя элемента
	const childs = [...parent.children];	// получаем всех потомков нашего родителя [т.к будем исп Find а он есть только у массива]

	const findMyIndex = (item) => {
		return item == this[0];	// фильтрационная ф-ия которая вернет индекс нашего элемента
	};

	return childs.findIndex(findMyIndex);	// применяет ф-ию на все элементы в массиве childs(на всех потомках общего родителя)
};

$.prototype.find = function(selector) {	// поиск элемента среди элементов с одинаковым селектором
	let numberOfItems = 0;		// кол-во элементов подходящих под передаваемый селектор
	let counter = 0;		// счетчик показывающих кол-во 
	 
	const copyObj = Object.assign({}, this);  // неглубокая копия нашего объекта 

	for (let i = 0; i < copyObj.length; i++) {
		const arr = copyObj[i].querySelectorAll(selector);	// ищем схожие элементы с селектором
		if (arr.length == 0) {	// если нет схожих элементов
			continue;
		}

		for (let j = 0; j < arr.length; j++) {
			this[counter] = arr[j];		// номер = элемент по селектору
			counter++;
		}

		numberOfItems += arr.length;
	}

	this.length = numberOfItems;

	const objLength = Object.keys(this).length;	// кол-во элементов в массиве
	for (; numberOfItems < objLength; numberOfItems++) {	// удаляет оставшиеся несхожие с селектором элементы из объекта
		delete this[numberOfItems];
	}

	return this;
};

$.prototype.closest = function(selector) {	// позволяет получить родителя элемента
	let counter = 0;

	for (let i = 0; i < this.length; i++) {
		if (this[i].closest(selector) === null) {
			return this;
		} else {
			this[i] = this[i].closest(selector);
			counter++;
		}
		
		
	}

	const objLength = Object.keys(this).length;	// удаляет все оставшиеся св-ва
	for (; counter < objLength; counter++) {	
		delete this[counter];
	}

	return this;
};

$.prototype.siblings = function() {	// соседние элементы общего родителя, без самого элемента
	let numberOfItems = 0;		// кол-во элементов подходящих под передаваемый селектор
	let counter = 0;		// счетчик показывающих кол-во 
	 
	const copyObj = Object.assign({}, this);  // неглубокая копия нашего главного объекта, чтобы избежать багов, работаем с ней

	for (let i = 0; i < copyObj.length; i++) {
		const arr = copyObj[i].parentNode.children; // получаем всех детей родителя

		for (let j = 0; j < arr.length; j++) {
			if (copyObj[i] === arr[j]) {		// тут речь о самом элементе на который применяется ф-ия
				continue;						// мы не добавляем его в конечный результат
			}									// то есть в итоге получим всех детей родителя нашего элемента, без самого элемента

			this[counter] = arr[j];		// номер = элемент по селектору
			counter++;
		}

		numberOfItems += arr.length - 1;	// здесь - 1 т.к мы не добавляем сам наш элемент в конечный результат
	}

	this.length = numberOfItems;

	const objLength = Object.keys(this).length;	// кол-во элементов в массиве
	for (; numberOfItems < objLength; numberOfItems++) {	// удаляет оставшиеся несхожие с селектором элементы из объекта
		delete this[numberOfItems];
	}

	return this;
};